
@font-face {
  font-family: tileFont;
  src: url('./Roboto/roboto_bold.ttf');
}

@font-face {
  font-family: titleFont;
  src: url('./Libre_Bodoni/static/LibreBodoni-Regular.ttf');
}

body{
  margin: 0px;
}

h1{
  font-family: titleFont;
}
.incorrect{
  background-color: #787c7e;
  color: white;
}

.correct-wrong-pos{
  background-color: #c9b458;
  color: white;
}

.correct{
  background-color: #6aaa64;
  color: white; 
}

$tileWidth: 4.5ch;
$tileHeight: 4.5ch;

$tileAnimWidth: 4.8ch;
$tileAnimHeight: 4.8ch;



@keyframes expand {
  0%{
    // width: $tileWidth;
    // height: $tileHeight;
    transform: scaleX(1) scaleY(1);
  }

  60%{
    // width: $tileAnimWidth;
    // height: $tileAnimHeight;
    transform: scaleX(1.1) scaleY(1.1);
  }

  100%{
    // width: $tileWidth;
    // height: $tileHeight;
    transform: scaleX(1) scaleY(1);
  }
}

.expand-start{
  animation: expand 0.05s linear forwards;
}

.border{
  border-color: black !important;
}

.noborder{
  border-color: transparent !important;
}

.border-gray{
  border-color: lightgray !important;
}

.navbar{
  display: flex;
  justify-content: center;
  border: 1px solid lavender;
  padding: 8px;
  font-family: tileFont;
  h1{
    margin: 0px;
  }
}

.game{
  // width: fit-content;
  // margin: auto;

  .grid{
    margin: auto;
    margin-top: 10%;
    width: fit-content;
  }

  .answer-row{
    margin-top: 36px;
    transition: all 0.5s ease-out;
  }
}

.invisible{
  opacity: 0;
}

.visible{
  opacity: 1;
}

.mx-auto{
  margin-left: auto;
  margin-right: auto;
}

.flex{
  display: flex;
}

.j-center{
  justify-content: center;
}

.row{
  display: flex;
  width: fit-content;

  .tile{
    border: 2px solid transparent;

    height: fit-content;
    width: 4.5ch;
    height: 4.7ch;
    display: flex;
    align-items: center;
    justify-content: center;
    // text-align: center;
    
    margin: 3px;
    padding:8px;
    
    p {
       font-family: tileFont;
       font-size: 1.8rem;
       margin: 0px;
       text-transform: capitalize;
       
    }
  }
}





 

